import React,{Component} from 'react';
import {  HashRouter ,Route , Switch } from 'react-router-dom';
import LeftSide from './components/LeftSide';
import PersonIndex from './components/PersonIndex'
import MyOrder from './components/MyOrder/index';
import UpdateInfo from './components/UpdataInfo/index';
import { connect } from 'react-redux';
import { actionCreators } from './store/index';
import {
    PersonWrapper,
    // MainSection
    
}from './style'
class Person extends  Component{
    constructor(props){
        super(props);
        let userInfo=this.props.location.state;
        if(userInfo){
            userInfo=JSON.parse(userInfo)
        }
        
        this.state={
            userInfo:userInfo||{}
            
        }
    }
    componentDidMount(){
        this.props.getGusLike();
        this.props.getRecentOrder(this.state.userInfo.phone);
        this.props.getUserInfo(this.state.userInfo.phone)
     
    }
    render(){
        return(
            <PersonWrapper>
         
                    <HashRouter>
                     <LeftSide/>
                        <Switch>
                          
                            <Route path="/person/updateInfo"  component={UpdateInfo}/>
                            <Route path="/person/myOrder"  component={MyOrder}/>
                            <Route path="/person"  component={PersonIndex}/>
                            
                           
                        </Switch>
                
                    </HashRouter>
               
            </PersonWrapper>
        )
    }
}
const mapState = (state) => ({
	user: state.getIn(['person', 'user']),
})

const mapDispatch = (dispatch) => ({
	getGusLike(){
        dispatch(actionCreators.getGusLike())

    },
    getRecentOrder(phone){
        dispatch(actionCreators.getRecentOrder(phone))
    },
    getUserInfo(phone){
        dispatch(actionCreators.getUserInfo(phone))
    }
})
export default connect(mapState,mapDispatch)(Person);